import React, { FC } from "react";

import { ReactComponent as GoldMedal } from "assets/icons/gold-medal.svg";
import { ReactComponent as SilverMedal } from "assets/icons/silver-medal.svg";
import { ReactComponent as CooperMedal } from "assets/icons/cooper-medal.svg";

import { Avatar, Img, LazyHider, LinkWithSearch, Typography } from "components";
import { LeaderboardUser } from "..";
import css from "./style.module.scss";

const medalList = [GoldMedal, SilverMedal, CooperMedal];
const avatarStyles = { width: 48, height: 48 };

interface Props extends LeaderboardUser {
  index: number;
}

const User: FC<Props> = ({
  net_profit_today,
  profile_picture_url,
  user_name,
  index,
}) => {
  const Badge = medalList[index];

  const LWS: FC = ({ children }) => (
    <LinkWithSearch
      search={{ name: "page", value: "profile" }}
      searchParams={{
        user: user_name,
      }}>
      {children}
    </LinkWithSearch>
  );

  return (
    <section key={index} className={css.minifiedWinner}>
      {Badge && (
        <div className={css.badge}>
          <Badge />
        </div>
      )}

      <LWS>
        <div className={css.logo}>
          <Avatar
            component={(props) => <Img {...props} />}
            userName={user_name}
            style={avatarStyles}
            avatar={profile_picture_url}
          />
        </div>
      </LWS>
      <LWS>
        <Typography className={css.userName} variant="h4">
          {user_name}
        </Typography>
      </LWS>
      <Typography className={css.profit} variant="h3">
        {`+${net_profit_today} USD`}
      </Typography>
    </section>
  );
};

export default User;
